.notification-modal {
    position: fixed;
    z-index: 1;
    padding-top: 10%; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4); 
    
  }
.notification-modal h1 {
  display: flex;
  justify-content: center;
  font-size: 3rem;
  color: brown;
  border: 2px double rgba(0,0,0,0.4);
  padding: 10%;
  margin: auto 10%;
}
